import React from 'react'
import "./style.css"
 export default  function CornerObtion(props)  {
    return (
        <div className="container">
            <center>
            <img className="img" alt="Profile Pic" src={props.img || "/defultProfile.jpg"} />
                <div className="tittle">{props.tittle || "Loading..."}</div>
            </center>
        </div>
    )
}
