import React, { useState } from "react";
import TextareaCodeEditor from "@uiw/react-textarea-code-editor";
// npm i @uiw/react-textarea-code-editor

import "./TextEditor.css";
import play from "./play.svg";
import Runelectron from "../../functions/Runelectron"
const exts = [
  "diff",
  "abap",
  "aes",
  "apex",
  "azcli",
  "bat",
  "brainfuck",
  "c",
  "cameligo",
  "clike",
  "clojure",
  "coffeescript",
  "cpp",
  "csharp",
  "csp",
  "css",
  "dart",
  "dockerfile",
  "erlang",
  "fsharp",
  "go",
  "graphql",
  "handlebars",
  "hcl",
  "html",
  "ini",
  "java",
  "javascript",
  "json",
  "jsx",
  "julia",
  "kotlin",
  "less",
  "lex",
  "livescript",
  "lua",
  "markdown",
  "mips",
  "msdax",
  "mysql",
  "nginx",
  "objective-c",
  "pascal",
  "pascaligo",
  "perl",
  "pgsql",
  "php",
  "plaintext",
  "postiats",
  "powerquery",
  "powershell",
  "pug",
  "python",
  "r",
  "razor",
  "redis",
  "redshift",
  "restructuredtext",
  "ruby",
  "rust",
  "sb",
  "scala",
  "scheme",
  "scss",
  "shell",
  "sol",
  "sql",
  "st",
  "stylus",
  "swift",
  "systemverilog",
  "tcl",
  "toml",
  "tsx",
  "twig",
  "typescript",
  "vb",
  "vbscript",
  "verilog",
  "vue",
  "xml",
  "yaml",
];

const TextEditor = (props) => {
  const [language, setLanguage] = useState(
    props.allowedLang
      ? props.allowedLang.includes(props.lang)
        ? exts.includes(props.lang)
          ? props.lang
          : props.allowedLang[0]
        : props.allowedLang[0]
      : (exts.includes(props.lang) ? props.lang : "jsx") 
  );
  const [code, setCode] = useState(props.code || "");
  return (
    <div className="TextEditor">
      <div className="TextEditor-editor">
        <TextareaCodeEditor
          autoFocus
          value={code}
          language={language}
          minHeight={650}
          placeholder={`Please enter ${(
            language || ""
          ).toLocaleUpperCase()} code.`}
          style={{
            backgroundColor: "#f5f5f5",
            fontSize: 14,
            fontFamily:
              "ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace",
          }}
          onChange={(evn) => setCode(evn.target.value)}
        />
      </div>
      <div className="TextEditor-tools" style={{ marginTop: 5 }}>
        <select
          value={language}
          onChange={(evn) => setLanguage(evn.target.value)}
        >
          {!props.allowedLang &&
            exts.map((keyName, idx) => {
              if (/^diff/.test(keyName)) return null;
              return (
                <option key={idx} value={keyName}>
                  Language: {keyName}
                </option>
              );
            })}
          {props.allowedLang &&
            props.allowedLang.map((keyName, idx) => {
              if (/^diff/.test(keyName)) return null;
              return (
                <option key={idx} value={keyName}>
                  Language: {keyName}
                </option>
              );
            })}
        </select>
        <div
          onClick={() => {
            alert("Running The Code in Language: "+language);
            
            Runelectron(()=>window.electron.runcode(code,language)).then(({stdout,stderr})=>alert(`${stdout?`Your Output:-${"\n"+stdout+"\n"}`:""}${stderr?`Error:-${"\n"+stderr}`:"Your Code Runs SucessFully"}`))
            // will Use Modal in Future
          }}
          className="click"
        >
          <img width="25px" height="25px" src={play} alt="" />
        </div>
      </div>
    </div>
  );
};

export default TextEditor;
