import "./App.css";
import Editor from "./screen/Editor";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./screen/Home";

function App() {
  return (
    <Router>
      <div className="app">
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/editor/">
            <Editor  />{/* <Editor allowedLang={["python","jsx"]}  />*/}
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
